export default function PartnerPage({title}) {
  const { partnerSlug } = useParams();

  const {
    data: partner,
    isFetching: isPartnerLoading,
    // isError: isPartnerError,
    // refetch: refetchPartner,
  } = useGetPartnerQuery(partnerSlug);
  // console.log(partner);

  const {
    data: categories,
    isFetching: isCategoriesLoading,
    isError: isCoursesError,
    // refetch: refetchCourses,
  } = useGetCategoriesQuery();

  const courses = {
    entities: [
      {
        title: '  ',
        price: '700000',
        'typecount': '',
        duration: '6 ',
        link: '',
      },
      {
        title: '  ',
        price: '700000',
        'typecount': '',
        duration: '6 ',
        link: '',
      },
      {
        title: '  ',
        price: '700000',
        'typecount': '',
        duration: '6 ',
        link: '',
      },
      {
        title: '  ',
        price: '700000',
        'typecount': '',
        duration: '6 ',
        link: '',
      },
      {
        title: '  ',
        price: '700000',
        'typecount': '',
        duration: '6 ',
        link: '',
      },
      {
        title: '  ',
        price: '700000',
        'typecount': '',
        duration: '6 ',
        link: '',
      },
    ]      
  }
  const isCoursesLoading = false;

  useDocumentTitle(title, partner?.title);

  const content = (

    <section className="main__instructor instructor container">
      {/*<MainSlider campaigns={campaigns} isLoading='' />*/}
      <PartnerInfo
        database={partner}
        isLoading={isPartnerLoading}
      />
      <div className="instructor__courses">
        <div className="instructor__block-filter">
          <SidebarCategoryFilter
            blockTitle = ''
            categories= {categories}
            isLoading = {isCategoriesLoading}
            isError={isCoursesError}
          />
        </div>

        <div className='instructor__block-courses'>
          <CoursesList
            courses={courses}
            isLoading={isCoursesLoading}
          />
        </div>
      </div>
    </section>
  );

  return (
    <PageLayout>
      {content}
    </PageLayout>
  );
}

PartnerPage.propTypes = {
  title: PropTypes.string.isRequired,
};